;AA5qCA;EACE;IACE,iBAAA;IACA,YAAA;EA+qCF;EA5qCA;IACE,iBAAA;IACA,YAAA;EA8qCF;EA3qCA;IACE,iBAAA;IACA,YAAA;EA6qCF;AACF;AAzqCE;EACE,iBAAA;AA2qCJ;AAxqCE;EACE,gBAAA;EACA,gBAAA;AA0qCJ;AAxqCI;EACE,6BAAA;AA0qCN;AAvqCI;EACE,iBAAA;AAyqCN;AAtqCI;EACE,YAAA;AAwqCN;;AAnqCA;EACE,YAAA;EACA,YAAA;EACA,WAAA;AAsqCF;AApqCE;EAIE,uCAAA;AAsqCJ;AA1qCE;EACE,2BAAA;EACA,8BAAA;EACA,6BAAA;AAuqCJ;AA9pCE;EAEE,uBAAA;AAiqCJ;AAnqCE;EACE,4BAAA;EAEA,mBAAA;AAgqCJ;AA7pCE;EACE,uBAAA;AAiqCJ;AAlqCE;EAEE,4BAAA;EACA,mBAAA;AA+pCJ;AA5pCE;EACE,4BAAA;AAgqCJ;AAjqCE;EAEE,mCAAA;EACA,mBAAA;AA8pCJ;AA3pCE;EACE,4BAAA;AA+pCJ;AAhqCE;EAEE,iCAAA;EACA,mBAAA;AA6pCJ;;AAzpCA;EACE,aAAA;EACA,mBAAA;AA4pCF;;AAzpCA;EACE,WAAA;EACA,YAAA;EACA,eAAA;EACA,sBAAA;AA4pCF;AA1pCE;EACE,eAAA;EACA,sBAAA;EACA,YAAA;AA4pCJ;;AAvpCE;EACE,gBAAA;EACA,gBAAA;AA0pCJ;AAvpCE;EACE,gBAAA;EACA,gBAAA;AAypCJ;AAtpCE;EACE,eAAA;EACA,eAAA;AAwpCJ;AArpCE;EACE,eAAA;EACA,eAAA;AAupCJ;AAppCE;EACE,gBAAA;EACA,gBAAA;AAspCJ","sourcesContent":["@import \"../main\";\n@import \"top_filters\";\n@import \"table_nav\";\n@import \"files_modal\";\n@import \"route_modal\";\n@import \"comments_modal\";\n@import \"planModal\";\n@import \"adminPanel\";\n\n.main-table {\n  &__header {\n    display: flex;\n    align-items: center;\n    position: sticky;\n    z-index: 10;\n    top: 0;\n    width: 100%;\n    //border: 1px solid black;\n    //border-top: none;\n  }\n\n  &__item {\n    display: flex;\n    align-items: center;\n  }\n}\n\ninput.table__data {\n  padding: 0 10px;\n}\n\ninput.tr {\n  padding: 0;\n}\n\n.table {\n  &__cell {\n    font-size: 14px;\n    font-weight: 400;\n    line-height: 18px;\n    letter-spacing: 0em;\n    text-align: left;\n    height: 28px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    cursor: default;\n    border-top: 1px solid black;\n    border-bottom: 1px solid black;\n    background: white;\n    color: $table_text;\n\n    &:not(:last-child) {\n      border-right: 1px solid black;\n    }\n\n    &--top {\n      background: $table_bg !important;\n    }\n  }\n\n  &:last-child {\n    border-right: none;\n  }\n\n  &-form {\n\n    input {\n      //color: $button_color;\n    }\n  }\n\n  &__use {\n    label {\n      cursor: pointer;\n      transition: color .3s;\n\n      &:hover {\n        color: $main_green;\n        transition: color .3s;\n      }\n    }\n  }\n\n  &__data {\n    font-size: 14px;\n    font-weight: 400;\n    height: 28px;\n    width: 100%;\n    text-align: center;\n    border-radius: 1px;\n    border: none;\n    background: white;\n    color: black;\n    outline: 3.3px $button_color;\n    position: relative;\n\n    &--ro {\n      cursor: default;\n      outline: none;\n    }\n\n    &--chosen {\n      //font-size: 15px;\n      background: $table_bg_main;\n      color: $table_text;\n    }\n\n    &--opened {\n      height: 56px;\n      font-size: 15px;\n      background: $table_bg_main;\n      color: $table_text;\n      border-bottom: 1px solid black;\n    }\n\n    &--opened.table__data--current {\n      border-bottom: 2px solid black;\n    }\n\n    &--current {\n      font-weight: 600;\n      color: $table_text;\n      border: 1px solid black;\n      //font-size: 16px;\n    }\n  }\n\n  &__db {\n    display: flex;\n    min-width: 70px;\n    max-width: 70px;\n  }\n\n  &__timestamp {\n    min-width: 100px;\n    max-width: 100px;\n  }\n\n  &__files {\n    min-width: 32px;\n    max-width: 32px;\n    position: relative;\n  }\n\n  &__number {\n    min-width: 80px;\n    max-width: 80px;\n  }\n\n  &__sample {\n    min-width: 60px;\n    max-width: 60px;\n  }\n\n  &__client {\n    min-width: 160px;\n    max-width: 160px;\n  }\n\n  &__name {\n    min-width: 260px;\n    max-width: 260px;\n  }\n\n  &__material {\n    min-width: 140px;\n    max-width: 140px;\n  }\n\n  &__quantity {\n    min-width: 70px;\n    max-width: 70px;\n  }\n\n  &__issued {\n    min-width: 70px;\n    max-width: 70px;\n    border-right: 1px solid black !important;\n\n    &--done {\n      color: green;\n      animation: issued-ready infinite 4s;\n    }\n  }\n\n  &__complete {\n    border-right: none !important;\n    border-top: 1px solid black;\n    border-bottom: 1px solid black;\n\n    input {\n      height: 26px;\n    }\n  }\n\n  &__m {\n    min-width: 36px;\n    max-width: 36px;\n  }\n\n  &__endtime {\n    min-width: 140px;\n    max-width: 140px;\n  }\n\n  &__route {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    height: 29px;\n    width: 45px;\n\n    &:last-child {\n      border-right: 1px solid black !important;\n    }\n  }\n\n  &__routes {\n    min-width: 450px;\n    max-width: 450px;\n  }\n\n  &-routes__issued {\n    //border-top: 1px solid black;\n  }\n\n  &__p {\n    min-width: 60px;\n    max-width: 60px;\n  }\n\n  &-p-select,\n  &-m-select {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    cursor: pointer;\n  }\n\n  &__comment {\n    flex-grow: 1;\n    min-width: 200px;\n    max-width: 100%;\n  }\n\n  &-routes {\n    &__wrapper {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      height: 28px;\n    }\n  }\n}\n\n.files__ico {\n  width: 30px;\n  height: 20px;\n}\n\n.table-body {\n  //&__trattr {\n  //  position: relative;\n  //\n  //  &:hover:after {\n  //    box-sizing: border-box;\n  //    min-width: 260%;\n  //    width: auto;\n  //    text-align: center;\n  //    content: attr(data-title);\n  //    position: absolute; /* Абсолютное позиционирование */\n  //    left: 0;\n  //    top: 130%; /* Положение подсказки */\n  //    z-index: 1; /* Отображаем подсказку поверх других элементов */\n  //    background: rgba(255, 255, 230, 0.9); /* Полупрозрачный цвет фона */\n  //    font-family: Arial, sans-serif; /* Гарнитура шрифта */\n  //    font-size: 11px; /* Размер текста подсказки */\n  //    padding: 5px 5px; /* Поля */\n  //    border: 1px solid #333;\n  //  }\n  //}\n  //\n  //&__attr {\n  //  position: relative;\n  //\n  //  &:hover:after {\n  //    box-sizing: border